import { Grid, Switch, FormControlLabel } from '@material-ui/core';
import { useSelector, useDispatch } from 'react-redux';

import { updateAsset } from '../../../redux/assetsSlice';
import { isEdited } from '../../../redux/configSlice';

export default function Taxable(props) {
  const { assetData } = useSelector(({ assets }) => assets);
  const dispatch = useDispatch();

  const { index } = props;

  const { taxable = true } = assetData[index];

  const updateTaxable = () => {
    dispatch(updateAsset({ updates: { taxable: !taxable }, index }));
    dispatch(isEdited());
  };

  return (
    <Grid item xs={12} sm={6} md={4}>
      <FormControlLabel
        control={<Switch checked={taxable} onChange={updateTaxable} />}
        label={'Taxable?'}
        labelPlacement='start'
      />
    </Grid>
  );
}
