import { Switch, FormControlLabel, Typography, makeStyles } from "@material-ui/core";
import { useSelector, useDispatch } from "react-redux";

import { isRebalance, isNotRebalance, isEdited } from "../../redux/configSlice";

const useStyles = makeStyles(() => ({
  root: {
    color: 'black',
    marginLeft: 0,
  }
}))

export default function Rebalance() {
  const { rebalance } = useSelector(({ config }) => config);
  const dispatch = useDispatch();

  const classes = useStyles();

  const onChange = (event) => {
    if (event.target.checked) {
      dispatch(isRebalance());
    } else {
      dispatch(isNotRebalance());
    }
    dispatch(isEdited());
  }

  return (
    <div>
      <FormControlLabel
        className={classes.root}
        control={
          <Switch
            checked={rebalance}
            onChange={onChange}
            color='primary'
          />
        }
        label={<Typography variant='subtitle'>Rebalance?</Typography>}
        labelPlacement="start"
      />
    </div>
  );
}